import * as React from "react"
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import {Avatar, Container, Grid, Link} from "@mui/material";
import Modal from '@mui/material/Modal';

import {BrowserView, MobileView} from 'react-device-detect';

import EmailIcon from "../images/email.png";
import GithubIcon from "../images/github.png";
import LinkedinIcon from "../images/linkedin.png";
import ResumeFile from "../images/resume.pdf";

const imageStyle = {
    width: "50px"
};

const modalStyle = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    bgcolor: 'background.paper',
    width: "90%",
    height: "90%",
}


const Layout: React.FC = ({children}) => {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <>
            <Toolbar>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                    Dragon Prevost
                </Typography>
                <BrowserView>
                    <Button color="inherit" onClick={handleOpen}>Resume</Button>
                </BrowserView>
                <MobileView>
                    <Button color="inherit" href={ResumeFile} target={"_blank"}>Resume</Button>
                </MobileView>
            </Toolbar>
            {children}
            <Modal open={open} onClose={handleClose}>
                <Box sx={modalStyle}>
                    <object data={ResumeFile} type="application/pdf" height={"100%"} width={"100%"} />
                </Box>
            </Modal>
        </>
    );

}

// markup
const IndexPage = () => {
  return (
      <Layout>
          <Container
              maxWidth={"sm"}
          >
            <Grid
                container
                spacing={0}
                direction={"column"}
                justifyContent={"center"}
                style={{ alignItems: "center", justifyContent: "center", display: "flex", minHeight: '80vh'}}
            >
                <Grid item xs={3} mb={5}>
                    <Avatar
                        alt={"Dragon Prevost"}
                        src={"https://avatars2.githubusercontent.com/u/22180881?s=460&v=4"}
                        sx={{width: 250, height: 250}}
                    />
                </Grid>
                <Grid item mt={5}>
                    <Grid container spacing={10}>
                        <Grid item xs={4}>
                            <a href={"https://github.com/dragonprevost"}>
                                <img src={GithubIcon} style={imageStyle} alt={"github"} />
                            </a>
                        </Grid>
                        <Grid item xs={4}>
                            <a href={"https://www.linkedin.com/in/dragonprevost/"}>
                                <img src={LinkedinIcon} style={imageStyle} alt={"linkedin"} />
                            </a>
                        </Grid>
                        <Grid item xs={4}>
                            <a href={"mailto:dragon@dkp.io"}>
                                <img src={EmailIcon} style={imageStyle} alt={"mail"} />
                            </a>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
          </Container>
      </Layout>
  );
}

export default IndexPage
